import { html } from 'lit';

export default {
  title: 'Mega Puzzle/Button',
  component: 'Button',
  id: 'btn',
  args: { label: "I'm an anchor tag", href: "https://reeceneedham.com", open_in_new_tab: false}
};

export const anchor = {
  args: { label: "I'm an anchor tag", href: "https://reeceneedham.com", open_in_new_tab: false},
  render: ({label, href, open_in_new_tab}) => {
    return open_in_new_tab ? html`<a href="${href}" target="_blank" rel="noopener">${label}</a>`: html`<a href="${href}">${label}</a>`;
  }
};

export const button = {
  args: { label: "I'm a button!"},
  render: ({label}) => html`<button>${label}</button>`
};
